
.recent-component {
    text-align: center;
    padding: 20px;
    margin-bottom: 50px;
    background: rgba(159, 145, 145, 0.1);
    // background: -webkit-linear-gradient(20deg,#f79533,#f37055,#ef4e7b,#a166ab,#5073b8,#1098ad,#07b39b,#6fba82);
    border-radius: 18px;
    font-size: 1rem;
    color: black;
    opacity: 0.9;
    font-weight: 600;
    .title-component {
        color: white !important;
    }
    .list {
        line-height: 2rem;
    }
    a {
        font-weight: 800;
        text-decoration: none;
        font-style: italic;
        background: -webkit-linear-gradient(60deg,#f79533,#f37055,#ef4e7b,#a166ab,#5073b8,#1098ad,#07b39b,#6fba82);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;

        &:hover {
            // text-decoration: underline;
            opacity: 0.7;
        }
    }
}
